import react,{Component} from 'react';
import Card from './Card'

// {} we use this bracket if we are exporting some class which have multiple export classes
// for example in component class
// but in react class there is default export,which means it exports only one class
// if we are writing default we are going to export only
// this class from the file, if we would have not written this
// means we are going to export multiple classes from the file
// name of the class must be in capital letter
export default class Fourth extends Component{
    constructor(props)
    {
        //props:values passed from the place where this component was called
        // State: these are something to be handeled internally
        super(props);
        // if we are using or declaring any 
        //variable in or from constructor,this kewyord is used
        this.state={
            users:[],
            filteredUsers:[]
        }
    }
    
    Searchbyname=(name)=>{
        let tempUsers=this.state.users.filter((user)=>{
            return user.name.toLowerCase().includes(name.toLowerCase());
        })
        this.setState({filteredUsers:tempUsers});
    }
    // we generally use componentDidMount to fetch data from the network
    componentDidMount=()=>{
        // fetch is a function available in java script to use network resources(to call api)
        fetch("https://jsonplaceholder.typicode.com/users")
        .then(response=>response.json())
        .then((users1)=>{
            this.setState({users:users1});
            this.setState({filteredUsers:users1});
        })
        .catch((err)=>{console.log(err)});

        // to send data(post,put,delete) to api end point we can also use fetch
        // fetch("url",{
        //     method:"POST",
        //     body:{},
        //     headers:{}
        // })
        // .then()
    }
    render(){
        return(
            <div className='container'>
                <div>
                    <input type="search" placeholder='Search BY Name' onChange={(event)=>{
                        this.Searchbyname(event.target.value)
                    }}></input>
                </div>
               {
                   this.state.filteredUsers.map((user,index)=>{
                       return(
                        <Card user={user} key={index}></Card>
                       )
                   }) 
               } 
            </div>    
        )
        
    }
}